<template>
  <div class="container">
    <div class="row">
      <div class="col-md-12">
        <div class="card">
          <div class="card-body">
            <div class="row">
              <div class="col-md-12">
                <div class="row">
                  <div class="col-md-12">
                    <p></p>
                    <a
                      href="https://dungeon.wombat.app/nfts"
                      target="_blank"
                      rel="noopener noreferrer"
                      >https://dungeon.wombat.app/nfts ({{
                        lastupdate.lastUpdate
                      }})</a
                    >
                  </div>
                  <div class="col-md-3">
                    <p>Price in USD: ${{ usdprice }}</p>
                    <label class="form-label">Symbol:</label>
                    <select v-model="symbol" class="form-select">
                      <option value="EOS" selected>EOS</option>
                      <option value="WAX">WAX</option>
                    </select>
                    <label class="form-label">Sort by:</label>
                    <select v-model="sortby" class="form-select">
                      <option value="usd_mp" selected>USD/MP</option>
                      <option value="price">Price</option>
                      <option value="mp">Mining Power</option>
                    </select>

                    <label class="typo__label">Materials: </label>
                    <multiselect
                      v-model="selected_material"
                      :options="material_list"
                      :multiple="true"
                      :close-on-select="false"
                      :clear-on-select="false"
                      :preserve-search="true"
                      placeholder="All Materials"
                      label="name"
                      track-by="name"
                      :preselect-first="false"
                    >
                      <template slot="selection" slot-scope="{ values, isOpen }"
                        ><span
                          class="multiselect__single"
                          v-if="values.length &amp;&amp; !isOpen"
                          >{{ values.length }} options selected</span
                        ></template
                      >
                    </multiselect>

                    <label for="customRange1" class="form-label"
                      >Minimum MP:</label
                    >
                    <input
                      type="number"
                      class="form-control"
                      :min="range.min"
                      :max="range.max"
                      step="5"
                      v-model="minMP"
                    />

                    <label class="form-label"
                      >Pages (100 assets per page):</label
                    >
                    <input type="number" v-model="page" class="form-control" />
                  </div>
                </div>
              </div>
              <div class="col-md-3">
                <div>
                  <label class="typo__label">Collections: </label>
                  <multiselect
                    v-model="selected_collection"
                    :options="collections"
                    :multiple="true"
                    :close-on-select="false"
                    :clear-on-select="false"
                    :preserve-search="true"
                    placeholder="All Collection"
                    label="name"
                    track-by="name"
                    :preselect-first="false"
                  >
                    <template slot="selection" slot-scope="{ values, isOpen }"
                      ><span
                        class="multiselect__single"
                        v-if="values.length &amp;&amp; !isOpen"
                        >{{ values.length }} options selected</span
                      ></template
                    >
                  </multiselect>
                  <!-- <pre class="language-json"><code>{{ selected_collection  }}</code></pre> -->
                </div>
              </div>
              <div class="col-md-3">
                <label class="typo__label"></label><br />
                <button class="btn btn-success" v-on:click="searchlist()">
                  <b-icon icon="search"></b-icon>
                </button>
              </div>
              <div class="col-md-3"></div>
              <div class="col-md-12">
                <p v-show="isLoading" class="text-center">
                  Loading Page: {{ current_page }}/{{ page }}
                </p>
                <hr />

                <vuetable
                  :css="css.table"
                  ref="vuetable"
                  :fields="fields"
                  :api-mode="false"
                  :data="list"
                  :sort-order="sortOrder"
                ></vuetable>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vuetable from "vuetable-2";
import listData from "../assets/list.json";
import lastupdate from "../assets/date.json";
import CssForBootstrap4 from "../assets/js/tablecss";
import Multiselect from "vue-multiselect";

export default {
  name: "ListTable",
  components: {
    Vuetable,
    Multiselect,
  },
  props: {},
  data() {
    return {
      minMP: null,
      range: {
        min: 5,
        max: 10000,
      },
      material_list: [{ name: "Uranium" }, { name: "Iron" }, { name: "Coal" }],
      selected_material: [],
      lastupdate,
      isLoading: false,
      fullPage: true,
      css: CssForBootstrap4,
      listData: listData,
      symbol: "EOS",
      fields: [
        {
          name: "supply",
          title: "Supply",
        },
        {
          name: "collection",
          title: "Collection",
        },
        {
          name: "template_id",
          title: "Template ID",
        },
        {
          name: "mint",
          title: "Mint",
        },
        {
          name: "name",
          title: "Name",
        },
        {
          name: "usd_price",
          title: "USD price",
        },
        {
          name: "price",
          title: "Price",
          sortField: "price",
        },
        {
          name: "mp",
          title: "Mining Power",
        },
        {
          name: "usd_mp",
          title: "USD/MP",
        },
        {
          name: "material",
          title: "Material",
        },
        {
          name: "link",
          title: "Link",
        },
      ],
      sortOrder: [
        {
          field: "price",
          direction: "asc",
        },
      ],
      list: [],
      collections: [],
      selected_collection: [],
      collection: "womplayextra",
      page: 1,
      current_page: 0,
      num_items: 100,
      items: [],
      usdprice: 0.0,
      sortby: "usd_mp",
      items_copy: [],
    };
  },
  created() {
    this.priceUsd();
    this.getCollections();
    this.getMPRange();
  },
  watch: {
    symbol: function () {
      this.priceUsd();
      this.getCollections();
    },
    sortby: function (x) {
      this.updateSort(x);
    },
    selected_material: function () {
      this.filterTable("material");
    },
    minMP: function (x) {
      if (x == null || x == "") {
        this.minMP = this.range.min;
      }

      this.filterTable("material");
    },
  },
  methods: {
    getMPRange: function () {
      this.range.min = Math.min(...this.listData.map((o) => o.mining_power));
      this.range.max = Math.max(...this.listData.map((o) => o.mining_power));
      this.minMP = this.range.min;
      console.log("range", this.range);
    },
    updateSort: function () {
      var varsort = this.sortby;
      this.items.sort((a, b) => {
        if (varsort == "price") {
          return a.price - b.price;
        }
        if (varsort == "mp") {
          return a.mp - b.mp;
        }
        if (varsort == "usd_mp") {
          return a.usd_mp - b.usd_mp;
        }
      });

      this.list = this.items;
    },
    filterTable: function (filter) {
      var newData = this.items_copy;
      var tlist = [];
      var tMP = this.minMP;

      if (filter == "material") {
        console.log("filter: material")
        if (this.selected_material.length > 0) {
          var val = [];
          this.selected_material.forEach(function (item) {
            val.push(item.name);
          });

          newData.forEach(function (item) {
            if (val.includes(item.material_name) && item.mp >= tMP) {
              tlist.push(item);
            }
          });

          this.list = tlist;
        } else {
          newData.forEach(function (item) {
            if (item.mp >= tMP) {
              tlist.push(item);
            }
          });
          this.list = tlist;
        }
      }
    },
    getCollections: function () {
      this.collections = [];
      var tcol = this.collections;
      var tsym = this.symbol;
      this.listData.forEach((x) => {
        if (x.symbol == tsym) {
          if (!tcol.some((el) => el.name === x.collection)) {
            // if(!this.collections.includes({"name": x.collection})){
            tcol.push({ name: x.collection });
          }
        }
      });

      this.collections.sort();
    },
    concatCollection: function () {
      var temp_collection = [];
      this.collection = null;

      if (this.selected_collection.length > 0) {
        this.selected_collection.forEach((x) => {
          temp_collection.push(x.name);
        });
        this.collection = temp_collection.join(",");
      } else {
        this.collections.forEach((x) => {
          temp_collection.push(x.name);
        });
        this.collection = temp_collection.join(",");
      }
    },
    searchlist: async function () {
      var url;
      this.concatCollection();
      var TotalData = [];
      var datalen = 100;
      this.current_page = 0;

      this.isLoading = true;
      let loader = this.$loading.show({
        container: null,
        canCancel: false,
      });

      while (datalen == 100 && this.current_page != this.page) {
        this.current_page++;
        url =
          "https://" +
          this.symbol +
          ".api.atomicassets.io/atomicmarket/v1/sales?collection_name=" +
          this.collection +
          "&page=" +
          this.current_page +
          "&limit=" +
          this.num_items +
          "&state=1&order=desc&sort=created";
        console.log(url);
        const response = await fetch(url);
        var data = await response.json();

        TotalData = TotalData.concat(data["data"]);
        datalen = data["data"].length;
        console.log("alldata", TotalData.length);
      }

      this.isLoading = false;
      loader.hide();

      this.drawTable(TotalData);
    },

    drawTable: async function (data) {
      await this.priceUsd();
      var ts = this;
      var t = (this.items = []);
      var dv = this.symbol == "EOS" ? 10000 : 100000000;

      data.forEach((x) => {
        var name =
          "name" in x["assets"][0]
            ? x["assets"][0]["name"]
            : x["assets"][0]["collection"]["name"];
        var price_symbol = parseInt(x["price"]["amount"]) / dv;
        var usd_value = price_symbol * this.usdprice;
        var getListData = this.getListData(x);
        var mp = getListData["mining_power"];
        var material = getListData["material"];
        var usdmp = (usd_value / mp).toFixed(4);

        if (mp > 0) {
          t.push({
            collection: x["assets"][0]["collection"]["name"],
            template_id: x["assets"][0]["template"]["template_id"],
            mint: x["assets"][0]["template_mint"],
            supply:
              x["assets"][0]["template"]["issued_supply"] +
              "/" +
              x["assets"][0]["template"]["max_supply"],
            name: name,
            link:
              "<a href='https://" +
              ts.symbol +
              ".atomichub.io/market/sale/" +
              x["sale_id"] +
              "' target='_blank' class = 'btn btn-dark'><img height='20px' src='https://wax.atomichub.io/images/logos/atomic.svg'></a>",
            price: price_symbol,
            usd_price: "$" + usd_value.toFixed(2),
            mp: mp,
            usd_mp: usdmp,
            material:
              material != null
                ? "<img height='30px' src='" +
                  require("@/assets/img/" + material + ".svg") +
                  "'>"
                : "",
            material_name: material,
          });
        }
      });
      this.list = [];

      this.updateSort();
      this.items_copy = this.items;
      this.list = this.items;

      this.filterTable("material");
    },
    priceUsd: async function () {
      var url =
        "https://" +
        this.symbol +
        ".api.atomicassets.io/atomicmarket/v1/config";
      const response = await fetch(url);
      const data = await response.json();
      this.usdprice =
        parseInt(data["data"]["supported_pairs"][0]["data"]["median"]) / 10000;
    },
    getListData: function (x) {
      if (x["assets"][0]["template"] != null) {
        var temId = parseInt(x["assets"][0]["template"]["template_id"]);
        var coll = x["assets"][0]["collection"]["collection_name"];
        var index = this.listData.findIndex(
          (x) => x.template === temId && x.collection === coll
        );
        if (index != -1) {
          return {
            mining_power: parseInt(this.listData[index]["mining_power"]),
            material: this.listData[index]["material"],
          };
        }
      }
      return { mining_power: 0, material: null };
    },
  },
};
</script>

